<template>
    <nav-bar>
      <span slot="title" class="title">菜色管理</span>
      <a slot="right-content" class="nav-link btn btn-primary-outline btn-sm pull-xs-right" @click="showForm">
        <i class="fa fa-plus"></i> 添加
      </a>
    </nav-bar>
    <table class="table">
      <thead>
        <tr>
          <th>图片</th>
          <th>名称 <i class="fa fa-sort"></i></th>
          <th>价格 <i class="fa fa-sort"></i></th>
          <th><a @click="showCategoriesList">分类 <i class="fa fa-sort"></i></a></th>
          <th>
            销售量
          </th>
          <th>操作</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="product in showedProducts">
          <td>
            <img alt="店铺头像" class="img-circle" width="40" height="40" v-bind:src="product.avatar" />
          </td>
          <td>{{product.name}}</td>
          <td>￥{{product.price}}</td>
          <td>{{product.category.name}}</td>
          <td>{{product.sales_volume}}</td>
          <td>
            <a v-link="'/product/'+product.id" class="btn btn-primary-outline btn-sm">修改</a>
            <button @click="remove(product)" class="btn btn-primary-outline btn-sm">删除</button>
          </td>
        </tr>
      </tbody>
    </table>

    <new-object :show.sync="showModal">
      <span slot="header">创建新菜色</span>
      <div class="content-form" slot="body">
        <form @submit="create" enctype="multipart/form-data" accept-charset="UTF-8" method="post">
          <div class="form-group row">
            <label for="inputEmail3" class="col-sm-2 form-control-label">名称</label>
            <div class="col-sm-10">
              <input class="form-control" placeholder="名称" type="text" v-model="newProduct.name" />
            </div>
          </div>
          <div class="form-group row">
            <label for="inputPassword3" class="col-sm-2 form-control-label">价钱</label>
            <div class="col-sm-10">
              <input class="form-control" placeholder="价钱" type="text" v-model="newProduct.price" />
            </div>
          </div>
          <div class="form-group row">
            <label for="inputPassword3" class="col-sm-2 form-control-label">分类</label>
            <div class="col-sm-10">
              <select class="c-select" v-model="newProduct.category_id">
                <option v-for="category in categories" track-by="$index" v-bind:value="category.id">
                  {{category.name}}
                </option>
              </select>
            </div>
          </div>
          <div class="form-group row">
            <label for="inputEmail3" class="col-sm-2 form-control-label"></label>
            <div class="col-sm-10">
              <input type="submit" name="commit" value="保 存" class="btn btn-primary" />
            </div>
          </div>
        </form>
      </div>
    </new-object>
    <new-object :show.sync="showCategoriesListModal">
      <span slot="header">选择分类</span>
      <div class="content-form" slot="body">
        <div class="form-group row">
          <label for="inputPassword3" class="col-sm-2 form-control-label">分类</label>
          <div class="col-sm-10" style="padding-top:8px;">
            <label style="margin-right:10px;">
              <input type="radio" v-bind:value="0" v-model="categoryPicked">全部
            </label>
            <label v-for="category in categories" track-by="$index" style="margin-right:10px;">
              <input type="radio" v-bind:value="category.id" v-model="categoryPicked">{{category.name}}
            </label>
          </div>
        </div>
        <div class="form-group row">
          <label for="inputEmail3" class="col-sm-2 form-control-label"></label>
          <div class="col-sm-10">
            <button @click="filterByCategory" class="btn btn-primary">搜 索</button>
          </div>
        </div>
      </div>
    </new-object>
</template>
<script>
  import { showAlert, showLoading, hideLoading } from '../vuex/actions'

  export default {
    vuex: {
      getters: {
        isLogged: state => state.merchant.isLogged,
        access_token: state => state.merchant.merchant.access_token
      },
      actions: {
        showAlert,
        showLoading,
        hideLoading
      }
    },
    data () {
      return {
        showModal: false,
        showCategoriesListModal: false,
        products: [],
        showedProducts: [],
        newProduct: {
          name: '',
          price: 0.0,
          category_id: 0
        },
        categories: {},
        categoryPicked: 0
      }
    },
    route: {
      data (transition) {
        this.showLoading()
        this.$http.get('http://jiancan.me/api/m1/shops/products.json', { access_token: this.access_token }).then(function (response) {
          this.$set('products', response.data)
          this.showedProducts = this.products
          // this.showedProducts = this.products.slice((this.page - 1) * this.offset, this.offset)
        }, function (response) {
          this.showAlert(response.data.error)
        })
        this.$http.get('http://jiancan.me/api/m1/shops/categories.json', { access_token: this.access_token }).then(function (response) {
          this.$set('categories', response.data)
        }, function (response) {
          this.showAlert(response.data.error)
        })
        this.hideLoading()
      }
    },
    methods: {
      showForm () {
        this.$set('showModal', true)
      },
      create () {
        this.showLoading()

        var formData = new FormData()
        formData.append('access_token', this.access_token)
        formData.append('product[name]', this.newProduct.name)
        formData.append('product[price]', this.newProduct.price)
        formData.append('product[category_id]', this.newProduct.category_id)

        this.$http.post('http://jiancan.me/api/m1/products.json', formData).then(function (response) {
          this.showedProducts.push(response.data)
          this.newProduct = { name: '', price: 0.0, category_id: 0 }
          this.showModal = false
        }, function (response) {
          this.showAlert(response.data.error)
        })
        this.hideLoading()
      },
      remove (product) {
        this.showLoading()
        this.$http.delete('http://jiancan.me/api/m1/products/one.json', { access_token: this.access_token, product_id: product.id }).then(function (response) {
          this.products.$remove(product)
        }, function (response) {
          this.showAlert(response.data.error)
        })
        this.hideLoading()
      },
      showCategoriesList () {
        this.showCategoriesListModal = true
      },
      filterByCategory () {
        this.showLoading()
        if (this.categoryPicked === 0) {
          this.showedProducts = this.products
        } else {
          this.showedProducts = this.products.filter(product => {
            return product.category_id === this.categoryPicked
          })
        }
        this.showCategoriesListModal = false
        this.hideLoading()
      }
    },
    components: {
      'nav-bar': require('../components/nav-bar.vue'),
      'new-object': require('../components/modal-mask/new-object.vue'),
      'pagination': require('../components/pagination')
    }
  }
</script>



