<template>
  <nav>
    <ul class="pagination">
      <li v-for="unit in units" @click="setPage(unit.page)" track-by="$index" :class="unit.class">
        {{{unit.html}}}
      </li>
    </ul>
  </nav>
</template>
<script>
  export default {
    props: {
      page: Number,
      total: Number,
      pageSize: Number,
      maxLink: Number
    },
    data () {
      return {
        hasPrev: false,
        hasNext: false
      }
    },
    methods: {
      setPage (page) {
        if (page === this.page) return false
        this.page = page
        this.$dispatch('page-change')
      }
    },
    computed: {
      units () {
        var page = this.page || 1
        var total = this.total || 0
        var pageSize = this.pageSize || 10
        var maxLink = this.maxLink > 5 ? this.maxLink : 5
        var linksCount = Math.ceil(total / pageSize)
        if (page > linksCount) page = linksCount + 0
        var realMaxLink = maxLink > linksCount ? linksCount : maxLink

        var units = []
        var arr = computeLens()

        for (var i = 0; i < arr.length; i++) {
          pushUnit(arr[i])
        }

        function pushUnit (i) {
          if (typeof i === 'number') {
            units.push({
              page: i,
              isPrev: false,
              isPager: false,
              disabled: false,
              class: i === page ? 'active page-item' : 'page-item',
              isNext: false,
              html: '<a class="page-link" href="javascript:void(0)">' + i + '</a>'
            })
          } else {
            units.push({
              class: 'page-item disabled',
              page: page,
              isPager: false,
              isPrev: false,
              isNext: true,
              html: '<a class="page-link disabled" href="javascript:void(0)">...</a>'
            })
          }
        }

        function computeLens () {
          var a4 = Math.floor((realMaxLink - 2) / 2)
          var a5 = realMaxLink - 3 - a4
          var s2 = page - a4
          var s3 = page + a5
          if (s2 < 2) {
            s2 = 2
          } else if (s3 > linksCount) {
            s2 = linksCount - (realMaxLink - 2)
          }
          var arr = [1]
          if (s2 > 2) arr.push('dot')
          var it
          for (var i = 0, len = realMaxLink - 2; i < len; i++) {
            it = i + s2
            arr.push(it)
          }
          if (it < linksCount - 1) arr.push('dot')
          if (it < linksCount) arr.push(linksCount)
          return arr
        }

        return units
      }
    }
  }
</script>



